.state-square{
    width: 280px;
    height: 150px;
    display: inline-block;
    margin: 20px;
    background-color: aquamarine;
    text-align: left;
    padding: 20px;
    position: relative;
}

@media only screen and (max-width: 600px) {
    body {
      background-color: lightblue;
    }
  }

.state-name{
    font-weight: 700;
}

.favorite{
    height: 25px;
    width: 25px;
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.lds-ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-ripple div {
    position: absolute;
    border: 4px solid rgb(141, 115, 115);
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes lds-ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
  
  .absolute{
      text-align: center;
  }